.testimonials {
  &__cards {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onTablet {
      gap: 30px;
    }
    
    @include onDesktop {
      flex-direction: row;
      gap: 20px;
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px 42px;

    background-color: $colorWhite;
    border-radius: 16px;
    transition: transform 0.5s;

    @include onDesktop {
      &:hover {
        transform: scale(1.05);
        -webkit-box-shadow: 0 0 35px -10px rgba(0,0,0,0.75);
        -moz-box-shadow: 0 0 35px -10px rgba(0,0,0,0.75);
        box-shadow: 0 0 35px -10px rgba(0,0,0,0.75);
      }
    }

    @include onLargeDesktop {
      padding: 32px 56px;
    }

    &-photo {
      width: 196px;
      height: 196px;
      margin-bottom: 27px;

      &--1 {
        background-image: url(../images/testimonials/Photo.svg);
      }

      &--2 {
        background-image: url(../images/testimonials/Photo2.svg);
      }

      &--3 {
        background-image: url(../images/testimonials/Photo3.svg);
      }
    }
  
    &-title {
      margin-bottom: 10px;
    }
  
    &-about {
      margin-bottom: 16px;
      max-width: 362px;

      font-family: "Open Sans", sans-serif;
      font-weight: 600;
      font-size: 13px;
      line-height: 150%;
      text-align: center;
      color: $colortext;

      @include onDesktop {
        margin-bottom: 32px;
      }
    }
  
    &-name {
      margin-bottom: 4px;

      font-family: "Open Sans", sans-serif;
      font-weight: 600;
      font-size: 13px;
      line-height: 150%;
      color: $colortitle;
    }

    &-position {
      font-family: "Poppins", sans-serif;
      font-weight: 700;
      font-size: 13px;
      line-height: 20px;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: #c0cdd7;
    }
  }

}
